import React, { FC, FormEvent } from 'react';
import styles from "./Register.module.css"
import { Input, PasswordInput, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, useNavigate } from "react-router-dom"
import { useFormValidation } from '../../hooks/useFormValidation';
import { Loader } from '../../components/loader/loader';
import { useDispatch, useSelector } from '../../services/types/hooks';
import { register } from '../../services/reducers/authSlice';
import { IAuthResult } from '../../utils/types';

const Register: FC = () => {

  const { values, errors, isValid, handleChange } = useFormValidation();
  const dispatch = useDispatch();
  const { loading, error } = useSelector(store => store.auth);
  const navigate = useNavigate();

  function handleSubmit(e: FormEvent<HTMLFormElement>) {
    e.preventDefault();
    dispatch(register({ email: values["email"], password: values["password"], name: values["name"] }))
      .then((res: unknown) => {
        if ((res as IAuthResult).payload) {
          const pathname = localStorage.getItem("redirectPath");
          if (pathname) {
            navigate(pathname, { replace: true });
          }
        }
      })
      .catch((err: string) => console.log(err))
  }

  return (
    <main className={styles.main}>
      <form className={styles.form} onSubmit={handleSubmit}>
        <h2 className={styles.title}></h2>
        <Input
          type={'text'}
          placeholder={''}
          onChange={handleChange}
          value={values["name"] || ""}
          name={'name'}
          error={errors["name"] ? true : false}
          errorText={errors["name"]}
          minLength={3}
          maxLength={20}
          required
        />
        <Input
          type={'email'}
          placeholder={'E-mail'}
          onChange={handleChange}
          value={values["email"] || ""}
          name={'email'}
          error={errors["email"] ? true : false}
          errorText={errors["email"]}
          size={'default'}
          extraClass="ml-1"
          required
        />
        <PasswordInput
          value={values["password"] || ""}
          name={'password'}
          extraClass="mb-2"
          onChange={handleChange}
          //@ts-ignore
          error={errors["password"] ? true : false}
          errorText={errors["password"]}
          minLength={8}
          maxLength={28}
          required
        />
        {loading ?
          <Loader size="medium" />
          :
          <Button
            htmlType="submit"
            type="primary"
            size="medium"
            extraClass={styles.btn}
            disabled={!isValid}
          >
            
          </Button>
        }
        {error && <span className='error'>{error}</span>}
        <p className={styles.text}> ?
          <Link to="/login" className={styles.link}> </Link>
        </p>
      </form>
    </main>
  )
}

export default Register;
